html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

button {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  padding: 87px 0 0 0;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 24px;
  font-family: var(--font-family, Arial, Helvetica, sans-serif);
  color: var(--light-color);
  font-weight: 400;
  background-color: var(--bg-dark);
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}
.page__body::after {
  content: "";
  display: block;
  position: absolute;
  right: 20%;
  width: 100%;
  max-width: 421px;
  height: 421px;
  background: rgb(0, 213, 228);
  filter: blur(200px);
  z-index: -1;
}
@media screen and (max-width: 768px) {
  .page__body::after {
    right: 0;
    top: 0;
    background: rgba(0, 213, 228, 0.8);
    max-width: 320px;
    height: 320px;
  }
}
@media screen and (max-width: 576px) {
  .page__body::after {
    max-width: 200px;
    height: 200px;
    background: rgb(0, 213, 228);
  }
}
img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  max-width: var(--container-width);
  padding: 0 20px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.ibg {
  position: relative;
}

.ibg img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.global-wrapper {
  position: relative;
}

.locked {
  overflow: hidden;
}

.title {
  text-transform: uppercase;
  font-size: 45px;
}
@media (max-width: 1480px) {
  .title {
    font-size: calc(30px + 15 * ((100vw - 320px) / 1160));
  }
}

.color-span {
  color: var(--primary-color);
}

.bordered-link {
  position: relative;
  font-size: 18px;
  color: var(--primary-color);
  line-height: 126%;
  font-weight: 700;
  padding: 10px 36px 10px 5px;
  border-top: 1px solid var(--primary-color);
  border-bottom: 1px solid var(--primary-color);
  transition: 0.5s all ease;
  max-width: 500px;
}
@media (max-width: 1480px) {
  .bordered-link {
    font-size: calc(13px + 5 * ((100vw - 320px) / 1160));
  }
}
.bordered-link:hover {
  background-color: var(--light-color);
}
.bordered-link::after {
  content: "";
  position: absolute;
  width: 29px;
  height: 8px;
  background-image: url("../img/icons/arrow.svg");
  background-repeat: no-repeat;
  background-size: contain;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.form input {
  background-color: var(--bg-lightdark);
  font-size: 16px;
  padding: 14px 12px;
  border: 1px solid var(--primary-color);
  color: #D9D9D9;
}
.form input::placeholder {
  color: #D9D9D9;
}

.btn {
  min-width: 250px;
  max-width: 300px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 14px;
  font-weight: 700;
  font-size: 16px;
  transition: 0.5s background ease;
}

.btn-primary {
  background-color: var(--primary-color);
  border: 1px solid var(--primary-color);
  text-transform: uppercase;
}
.btn-primary:hover {
  background-color: var(--light-color);
  color: var(--bg-dark);
}

.hide {
  display: none;
}

.callback {
  background-color: var(--bg-lightdark);
  padding-top: 48px;
  padding-right: 48px;
  padding-bottom: 48px;
  padding-left: 48px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1480px) {
  .callback {
    padding-top: calc(25px + 23 * ((100vw - 320px) / 1160));
  }
}
@media (max-width: 1480px) {
  .callback {
    padding-right: calc(25px + 23 * ((100vw - 320px) / 1160));
  }
}
@media (max-width: 1480px) {
  .callback {
    padding-bottom: calc(25px + 23 * ((100vw - 320px) / 1160));
  }
}
@media (max-width: 1480px) {
  .callback {
    padding-left: calc(25px + 23 * ((100vw - 320px) / 1160));
  }
}
.callback__title {
  color: var(--primary-color);
  margin-right: 115px;
  font-weight: 900;
  line-height: 140%;
  max-width: 410px;
}
@media screen and (max-width: 992px) {
  .callback__title {
    margin-right: 0;
    text-align: center;
    margin-bottom: 20px;
  }
}
.callback__inner {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 992px) {
  .callback__inner {
    flex-direction: column;
  }
}

.header {
  padding: 20px 0 24px 0;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 6;
  background-color: transparent;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  flex: 0 0 74px;
  margin-right: 30px;
  height: 43px;
  padding-top: 5px;
  z-index: 7;
}
@media screen and (max-width: 768px) {
  .header__logo {
    padding-top: 0;
  }
}
.header::after {
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #575555;
  z-index: 8;
}

@media screen and (max-width: 768px) {
  .menu {
    padding: 200px 0 30px 0;
    position: fixed;
    background-color: var(--bg-dark);
    width: 100%;
    height: 100%;
    left: 0;
    bottom: 0;
    z-index: 4;
    visibility: hidden;
    opacity: 0;
    transition: 0.5s all ease;
    overflow-y: auto;
  }
  .menu::before {
    content: "";
    top: 0;
    width: 100%;
    height: 85px;
    position: fixed;
    display: block;
    z-index: 20;
  }
}
.menu.menu--active {
  visibility: visible;
  opacity: 1;
}
.menu__list {
  display: grid;
  grid-template-columns: repeat(6, auto);
  column-gap: 35px;
  row-gap: 10px;
  max-width: 700px;
}
@media screen and (max-width: 768px) {
  .menu__list {
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
    row-gap: 36px;
  }
}
.menu__link {
  font-size: 16px;
  color: var(--light-color);
  position: relative;
  transition: 0.5s all ease;
}
.menu__link::before, .menu__link::after {
  content: "";
  position: absolute;
  width: 0;
  height: 1px;
  background-color: var(--primary-color);
  transition: 0.5s all ease;
}
.menu__link::before {
  top: 0;
  right: 0;
}
.menu__link::after {
  bottom: 0;
  left: 0;
}
.menu__link:hover {
  color: var(--primary-color);
}
.menu__link:hover::before, .menu__link:hover::after {
  width: 100%;
}
@media screen and (max-width: 768px) {
  .menu__link {
    font-size: 24px;
  }
}

.burger {
  --burger-width: 33px;
  --burger-height: 22px;
  --burger-line-height: 4px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: var(--primary-color);
  background-color: transparent;
  cursor: pointer;
  z-index: 5;
  display: none;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
  left: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
  width: 60%;
  right: 0;
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  height: 3px;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  width: 100%;
  height: 3px;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}
@media screen and (max-width: 768px) {
  .burger {
    display: block;
  }
}

.footer {
  background-color: #0C0A0A;
  padding: 30px 0;
  color: var(--light-color);
  font-size: 16px;
}
.footer__head {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 30px;
  align-items: center;
  padding-bottom: 23px;
  position: relative;
  margin-bottom: 25px;
}
@media screen and (max-width: 576px) {
  .footer__head {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}
.footer__head::after {
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: 0;
  background-color: #636161;
}
.footer__info {
  display: flex;
  flex-direction: column;
  max-width: 303px;
  grid-column: span 3;
}
@media screen and (max-width: 768px) {
  .footer__info {
    grid-column: span 5;
  }
}
@media screen and (max-width: 576px) {
  .footer__info {
    justify-self: start;
    grid-column: unset;
  }
}
.footer__info a {
  line-height: 141%;
}
.footer__logo {
  margin-bottom: 15px;
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  grid-column: 9/11;
  justify-self: end;
}
@media screen and (max-width: 768px) {
  .footer__contacts {
    grid-column: 6/11;
  }
}
@media screen and (max-width: 576px) {
  .footer__contacts {
    grid-column: unset;
    justify-self: start;
  }
}
@media screen and (max-width: 768px) {
  .footer__contacts-wrapper {
    display: flex;
    margin-bottom: 20px;
  }
}
@media screen and (max-width: 375px) {
  .footer__contacts-wrapper {
    flex-direction: column;
  }
}
.footer__contacts-item {
  display: flex;
  flex-direction: column;
}
.footer__contacts-item:not(:last-child) {
  margin-bottom: 15px;
}
@media screen and (max-width: 768px) {
  .footer__contacts-item:not(:last-child) {
    margin-right: 22px;
  }
}
.footer__contacts-item p {
  color: var(--primary-color);
  margin-bottom: 5px;
}
.footer__contacts-item a {
  position: relative;
  padding-bottom: 5px;
  align-self: flex-start;
  max-width: 160px;
  text-decoration: underline;
  word-break: break-all;
}
.footer__contacts-item a:hover {
  text-decoration: none;
}
.footer__socials {
  display: grid;
  gap: 15px;
  grid-template-columns: repeat(3, 48px);
  grid-column: 5/7;
  justify-self: center;
}
.footer__socials-item {
  transition: transform 0.5s ease;
}
.footer__socials-item svg path {
  transition: 0.5s all ease;
}
.footer__socials-item svg:hover path {
  fill: var(--primary-color);
}
@media screen and (max-width: 576px) {
  .footer__socials {
    grid-column: unset;
  }
}
.footer__bottom {
  text-align: center;
}
.footer__bottom a {
  position: relative;
  padding-bottom: 5px;
}
.footer__bottom a::after {
  content: "";
  position: absolute;
  width: 0%;
  height: 1px;
  background-color: var(--light-color);
  left: 0;
  bottom: 0;
  transition: 0.5s width ease;
}
.footer__bottom a:hover::after {
  width: 100%;
